@import url(https://fonts.googleapis.com/css?family=Merriweather:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  color: #03344a;
  font-weight: 300;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  text-align: center;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.question {
  margin: 0.2em;
}

.answerOptions {
  width: 70%;
  margin: 0 auto;
  list-style: none;
}

.answerOption {
  border-top: 1px solid #eee;
  text-align: center;
}

.answerOption:hover {
  background-color: #eefbfe;
}

.radioButton {
  position: absolute;
  width: auto;
  opacity: 0;
}

.radioButton,
.radioLabel {
  display: inline-block;
  vertical-align: left;
  cursor: pointer;
}

.radioLabel {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 1.5rem 2.5rem 1.5rem 1em;
  max-width: 350px;
  font-size: 1.2em;
  font-weight: 600;
  line-height: 1.5;
}

.radioButton ~ .radioLabel:before {
  position: absolute;
  top: 20px;
  left: 38px;
  width: 28px;
  height: 28px;
  content: '';
  display: inline-block;
  vertical-align: middle;
  background: #fff;
  border: 1px solid #bbb;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.radioButton:checked ~ .radioLabel:before {
  content: '';
  background: #8bc53f url(/troubleshooting-tool/static/media/icon-check.5e1cbdb5.svg) no-repeat;
  background-size: 27px;
  border-color: #8bc53f;
}

.questionCount {
  padding: 1.5rem 2.5rem 0;
  font-size: 14px;
}

.questionnaire {
  -webkit-transition: opacity 0.5s ease-in-out 0.3s;
  transition: opacity 0.5s ease-in-out 0.3s;
}

.buttonsGroup .button {
  margin: 1em;
}

.result {
  margin: 2em;
  font-weight: 600;
  line-height: 1.5;
  -webkit-transition: opacity 0.5s ease-in-out 0.3s;
  transition: opacity 0.5s ease-in-out 0.3s;
}

